// parent of AddTodo

<template>
  <div id="app">
    <AddTodo v-on:add-todo="addTodo" />
    <Todos v-bind:todos="todos" v-on:del-todo="deleteTodo" />
  </div>
</template>

<script>
import Todos from "../components/Todos";
import AddTodo from "../components/AddTodo";

export default {
  name: "Home",
  components: {
    Todos,
    AddTodo
  },

  data() {
    return {
      todos: [
        { id: 1, title: "Drink juice", isComplete: true },
        { id: 2, title: "Go to Office", isComplete: false },
        { id: 3, title: "Take lunch", isComplete: false },

        { id: 4, title: "Meeting ", isComplete: false },
        { id: 5, title: "Order milk ", isComplete: false }
      ]
    };
  },

  methods: {
    deleteTodo(id) {
      this.todos = this.todos.filter(val => val.id !== id);
    },
    addTodo(text) {
      let data = {
        id: Date.now(),
        title: text,
        isComplete: false
      };
      this.todos = [...this.todos, data];
    }
  }
};
</script>

