<template>
  <div>
    <form @submit.prevent="addTodo">
      <input type="text" v-model="title" name="title" placeholder="Add Todo..." />
      <input type="submit" value="Add " class="btn" />
    </form>
  </div>
</template>

<script>
// import uuid from 'uuid';
export default {
  name: "AddTodo",
  data() {
    return {
      title: ""
    };
  },

  methods: {
    addTodo() {
      this.$emit("add-todo", this.title);
      this.title = "";
    }
  }
};
</script>
    
<style scoped>
form {
  display: flex;
}

input[type="text"] {
  flex: 10;
  padding: 5px;
  margin: 10px;
}

input[type="submit"] {
  flex: 2;
}
</style>


